












































































































































































































































































































































import _ from 'lodash';

export default {
  layout: 'TextLayout',
  data() {
      return {
          colors: ['indigo lighten-4', 'amber lighten-2', 'pink lighten-4', 'red lighten-3', 'deep-purple lighten-3'],
          slides: ['', '', '', '', ''],
          fields: ['', '', '', '', ''],
          toggle: 0,
          from_lang: '',
          from_code: 'ko',
          to_lang: '',
          to_code: 'zh-chs',
          from_text: '',
          acceptFiles: '.txt,.pdf',
          test_file: '',
          from_file_lang: '',
          to_file_lang: '',
          from_file_code: '',
          to_file_code: '',
          file_text: '',
      }
  },
  created() {
    this.from_lang = this.language === '' ? '' : 'Korean';
    this.to_lang = this.language === '' ? '()' : 'Chinese(Simplified)';
    this.from_file_lang = this.language === '' ? '' : 'Korean';
    this.to_file_lang = this.language === '' ? '()' : 'Chinese(Simplified)';
  },
  computed: {
    language() {
      return this.$store.state.manager.language;
    },
    from_languages() {
      return this.$LANGUAGES_KO;
    },
    e_from_languages() {
      return this.$LANGUAGES_EN;
    },
    languages() {
      let newLanguages = this.$LANGUAGES_KO;
      if (newLanguages != undefined) newLanguages = newLanguages.filter(lang => lang != this.from_lang);
      return newLanguages;
    },
    e_languages() {
      let newLanguages = this.$LANGUAGES_EN;
      if (newLanguages != undefined) newLanguages = newLanguages.filter(lang => lang != this.from_lang);
      return newLanguages;
    },
    file_languages() {
      let newLanguages = this.$LANGUAGES_KO;
      if (newLanguages != undefined) newLanguages = newLanguages.filter(lang => lang != this.from_file_lang);
      return newLanguages;
    },
    e_file_languages() {
      let newLanguages = this.$LANGUAGES_EN;
      if (newLanguages != undefined) newLanguages = newLanguages.filter(lang => lang != this.from_file_lang);
      return newLanguages;
    },
    to_text() {
      return this.$store.state.manager.translateText;
    }
  },
  watch: {
    language: function(lang) {
      this.from_lang = lang === '' ? '' : 'Korean';
      this.to_lang = lang === '' ? '()' : 'Chinese(Simplified)';
      this.from_file_lang = this.language === '' ? '' : 'Korean';
      this.to_file_lang = this.language === '' ? '()' : 'Chinese(Simplified)';
    },
    from_lang: _.debounce(function(from) {
      this.from_code = this.language === '' ? this.$LANG_CODE[this.$LANGUAGES_KO.indexOf(from)] : this.$LANG_CODE[this.$LANGUAGES_EN.indexOf(from)];
      this.translate();
    }, 500),
    to_lang: _.debounce(function(to) {
      this.to_code = this.language === '' ? this.$LANG_CODE[this.$LANGUAGES_KO.indexOf(to)] : this.$LANG_CODE[this.$LANGUAGES_EN.indexOf(to)];
      this.translate();
    }, 500),
    from_file_lang: _.debounce(function(lang) {
      this.from_file_code = this.language === '' ? this.$LANG_CODE[this.$LANGUAGES_KO.indexOf(lang)] : this.$LANG_CODE[this.$LANGUAGES_EN.indexOf(lang)];
      if (this.from_text != "" || this.from_text != null) this.uploadFile(this.test_file);
    }, 500), 
    to_file_lang: _.debounce(function(lang) {
      this.to_file_code = this.language === '' ? this.$LANG_CODE[this.$LANGUAGES_KO.indexOf(lang)] : this.$LANG_CODE[this.$LANGUAGES_EN.indexOf(lang)];
      if (this.from_text != "" || this.from_text != null) this.uploadFile(this.test_file);
    }, 500),
    from_text: _.debounce(function(text) {
      this.translate();
    }, 500)
  },
  methods: {
    async translate() {
      if (this.from_lang.length == 0 || this.to_lang.length == 0) {
        this.$manage.showMessage({ message: ' .', color: 'red' });
      } else {
        if (this.from_text == "" || this.from_text == null) {
          this.$store.commit('manager/setTranslate', '');
        } else {
          try {
            this.$nuxt.$loading.start();
            const res = await this.$store.dispatch('manager/Test', {
              from: this.from_code,
              to: this.to_code,
              text: this.from_text,
              returnValue: false,
            });
            this.$nuxt.$loading.finish();
          } catch(err) {

          }
        }
      }
    },
    async uploadFile(file) {
      if (file != null && typeof(file) == 'object') {
        if ((file.size / 1000000) >= 5.5) {
          this.$manage.showMessage({ message: '   .', color: 'red' });
        } else {
          this.test_file = file;
          const filename = '' + this.test_file.name;
          const ext = filename.substring(filename.lastIndexOf('.') + 1, filename.length).toLowerCase();
          switch (ext) {
            case 'txt':
              //  
              this.$nuxt.$loading.start();
              let txt_text = await this.test_file.text();
              const txtResponse = await this.$store.dispatch('manager/Test', {
                from: this.from_file_code,
                to: this.to_file_code,
                text: txt_text,
                returnValue: true,
              });
              this.$nuxt.$loading.finish();
              this.file_text = txtResponse;
              break;
            case 'docx':
              //  
              /*const docxResponse = await this.$store.dispatch('manager/textExtract', { 
                file: this.test_file, 
                to: this.to_file_code 
              });
              console.log(docxResponse);*/
              break;
            case 'pdf':
              // pdf 
              this.$nuxt.$loading.start();
              const pdfResponse = await this.$store.dispatch('manager/textExtract', {
                from: this.from_file_code,
                to: this.to_file_code,
                file: this.test_file,
              });
              this.$nuxt.$loading.finish();
              //console.log(pdfResponse);
              this.file_text = pdfResponse;
              break;
            default:
              break;
          }
        }
      } else {
        this.file_text = '';
        this.test_file = '';
      }
    }
  }
}
